@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400,700|Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.panel {
  padding: 10px;
}

.horizontal-display > ul > li {
  display: block;
  float: right;
  margin-right: 15px;
}

.select-panel-1 {
  transition: 0.2s ease-in !important;
  cursor: pointer;
}

.select-panel-1:hover {
  color: white;
  background-color: #05c256;
}

.info-icon {
  transition: 0.2s ease-in !important;
}

.select-panel-1:hover .info-icon {
  color: white !important;
}

.select-panel-2:hover .info-icon {
  color: white !important;
}

.select-panel-3:hover .info-icon {
  color: white !important;
}

.select-panel-2 {
  transition: 0.2s ease-in !important;
  cursor: pointer;
}

.select-panel-2:hover {
  color: white;
  background-color: #00a4f4;
}

.select-panel-3 {
  transition: 0.2s ease-in !important;
  cursor: pointer;
}

.select-panel-3:hover {
  color: white;
  background-color: #ff5454;
}

html,
body {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

